<template>
  <div class="d-flex flex-column">
    <span>Table</span>

    <AddButton v-if="editable" @click="showModal = true">
      Добавить организацию
    </AddButton>

    <OrganizationModal
      :show="showModal"
      @submit="onSubmit"
      @close="onCloseModal"
      :initial-values="editedOrganization"
    />
  </div>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import AddButton from "@/components/buttons/AddButton";
import { isEditableStatus } from "@/helpers/StatusHelpers";
import OrganizationModal from "@/components/organizations/OrganizationModal";

export default {
  name: "OrganizationsTable",
  components: {
    AddButton,
    OrganizationModal,
  },
  data() {
    return {
      showModal: false,
      editedOrganization: undefined,
      editedOrganizationIndex: undefined,
    };
  },
  computed: {
    ...mapGetters("formInfo", {
      formStatus: "getStatus",
    }),
    ...mapGetters("organizations", {
      organizations: "getOrganizations",
    }),
    editable() {
      return isEditableStatus(this.formStatus);
    },
  },
  methods: {
    ...mapMutations("organizations", [
      "addOrganization",
      "removeOrganization",
      "updateOrganization",
    ]),
    onSubmit(organization) {
      if (this.editedOrganizationIndex >= 0) {
        this.updateOrganization({
          index: this.editedOrganizationIndex,
          organization,
        });
      } else {
        this.addOrganization(organization);
      }

      this.showModal = false;
      this.editedOrganization = undefined;
      this.editedOrganizationIndex = undefined;

      this.$emit("success");
    },
    onEdit(index) {
      this.editedOrganizationIndex = index;
      this.editedOrganization = this.organizations[index].name;
      this.showModal = true;
    },
    onDelete(index) {
      this.removeOrganization(index);
    },
    onCloseModal() {
      this.showModal = false;
      this.editedOrganization = undefined;
      this.editedOrganizationIndex = undefined;
    },
  },
};
</script>
